
<template>
    
    <div>
        <el-form ref="form" :model="form" >
            <el-form-item label="文本内容:">
                <el-input  type="textarea" :rows="2" placeholder="请输入内容" v-model="form.text" ></el-input>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="onSubmit">提交</el-button>
            </el-form-item>
        </el-form>
        <div>
            <el-tag>标签一</el-tag>
            <el-tag type="success">标签二</el-tag>
            <el-tag type="info">标签三</el-tag>
            <el-tag type="warning">标签四</el-tag>
            <el-tag type="danger">标签五</el-tag>
        </div>
    </div>
</template>

<script>
  import {  ref } from 'vue'
  const axios = require('axios')
  export default {
    setup() {
        return {
        textarea: ref('')
        }
    },
    data() {
        return {
            form: {
                text: ''
            }
        }
    },
     
    
    methods: {

        onSubmit() {
            axios({
                method: 'post',
                url: 'http://localhost:5000/ping',
                data: {'text':this.form.text},
                // 发送post请求时,不能发送 Content-Type: application/json;charset=UTF-8 这个格式,因为后台过滤器要进行处理签名
                headers: {
                    //'Content-Type': 'application/x-www-form-urlencoded',   // 设置请求头为form表单格式
                    'Content-Type': 'application/json;charset=UTF-8'
                },
            })
            .then((response) => {
                alert('功能未实现')
                console.log(response)
            })
            .catch((error) => {
                console.log(error)
            })

        }     
    }
  }
</script>

